          <div @click="$root.showWarning = false" class="light-button">我知道了</div>
        </div>
        <div class="content" v-else>
          <div>你的点亮次数已用完，活动链接内每天仅能点亮一次，登录小睡眠，可获得更多点亮次数。</div>
          <div @click="toDownLoad" class="light-button">去小睡眠点亮</div>
