    <div class="content-template">
      <img class="top-mask" src="../assets/top-mask.png"/>
      <div class="fans-avatar">
        <img class="avatar" :src="star_data.fans_info.fans_avatar + '?imageMogr2/auto-orient'" />
        <div class="circle" style="opacity: 0" id="circles-1"></div>
      </div>
      <div class="fans-name"></div>
      <div class="help-desc">
        <div class="top" v-if="clb">在小睡眠里</div>
        <div class="bottom" v-if="clb">每天为 <span style="color: RGBA(83, 155, 230, 1)">{{star_data.star_info.star_name}}</span> 点亮星光</div>
        <div class="top" v-if="!clb">快来帮 <span style="color: RGBA(83, 155, 230, 1)">{{star_data.star_info.star_name}}</span> 点亮星光</div>
        <div class="bottom" v-if="!clb">一起听爱豆哄睡故事吧</div>
      </div>
      <div class="light-button" :class="{ clickBefore: clb }" @click="submitData">{{ clb ? '享受完整哄睡故事' : '为他点亮' }}</div>
      <div class="friend" v-show="star_data.star_info.star_name"><span class="special-font">{{star_data.total_user}}</span> 位好友点亮星光 <span class="special-font">{{star_data.total_score}}</span></div>
