<script>
import Music from '../components/music.vue'
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import MyInput from '../components/Input.vue'
import { callAppRouter } from '@/js/tool.js'
import Tool from '../js/tool.js'
const Circles = require('../js/circles.js')
const QRCode = require('qrcode')

export default {
  name: 'home',
  data () {
    return {
      canClick: false, // 能否点亮？
      qrUrl: '',
      score: 20,
      clb: false,
      showTips: false,
      tipsImage: '',
      home_height: 0,
      star_data: {
        star_info: {},
        fans_info: {},
        total_user: '',
        lighten_list: {}
      }
    }
  },
  components: {
    Music
  },
  methods: {
    toDownLoad () {
      this.$root.showWarning = false
      window.location.href = 'https://www.heartide.com/download/smallsleep'
    },
    parseDate (v) {
      return this.WJH.dateFormat(+(v + '000'), 'yyyy-MM-dd')
    },
    toRem (v) {
      return +document.querySelector('html').style.fontSize.replace('px', '') * (+v / 37.5)
    },
    circleMounted () {
      const _circle = Circles.default()
      _circle.create({
        id: 'circles-1',
        radius: this.toRem(38),
        value: 21,
        maxValue: 100,
        width: this.toRem(3),
        text: function (value) { return '' },
        colors: ['rgba(255, 255, 255, 0.2)', '#F8C074'],
        duration: 400,
        wrpClass: 'circles-wrp',
        textClass: 'circles-text',
        valueStrokeClass: 'circles-valueStroke',
        maxValueStrokeClass: 'circles-maxValueStroke',
        styleWrapper: true,
        styleText: true
      })
    },
    submitData () {
      // 真正有数据的时候要删除掉
      // this.$root.test()
      if (!localStorage.getItem('name') || !localStorage.getItem('openid') || localStorage.getItem('openid') === 'undefinded') {
        // || this.$root.test('avatar')
        // if (Tool.is_cosleep()) {
        //   callAppRouter('Login', {}, (res, ed) => {})
        //   return
        // }
        console.log('data config')
        this.$root.shareM(`我正在帮${this.star_data.star_info.star_name}收集星光`, '求助攻，一起点亮璀璨星光吧！', '')
        this.$root.test()
        return
      }
      // if (this.clb) {
      //   // 那个模板show
      //   window.location.href = 'https://www.heartide.com/download/smallsleep'
      //   return
      // }
      this.$axios.post('/web/v1/cosleep/sleep/star/visitor/lighten', { fans_id: this.star_data.fans_info.fans_id, openid: localStorage.getItem('openid'), visitor_name: localStorage.getItem('name') || 2, visitor_avatar: localStorage.getItem('avatar') || 2 }).then((res) => {
        const status = res.data.status
        if (status === 1) {
          this.clb = true
          this.showTips = true
          this.tipsImage = res.data.data.visitor_avatar
          this.score = res.data.data.visitor_score
          this.star_data.lighten_list.unshift(res.data.data)
        } else {
          this.canClick = true
          this.$root.showWarning = true
        }
      })
    },
    getData () {
      this.circleMounted()
      console.log(
        this.WJH.parseQuery(window.location.href).fans_id, '12312312312'
      )
      const fans_id = this.WJH.parseQuery(window.location.href).fans_id || localStorage.getItem('fansid')
      localStorage.setItem('fansid', fans_id)
      console.log(fans_id)
      this.$axios.get('/web/v1/cosleep/sleep/star/visitor', { params: { fans_id } }).then(res => {
        if (res.data.status !== 1) {
          return
        }
        setTimeout(() => {
          this.$root.showLoading = false
        }, 1000)
        const data = res.data.data
        // if (data.today_lighten === 0) {
        //   this.clb = false
        // }
        this.star_data = data
        this.$root.shareM(`我正在帮${this.star_data.star_info.star_name}收集星光`, '求助攻，一起点亮璀璨星光吧！', data.star_info.star_share_bg)
        // this.$root.test(true) // 后面妖姬
        this.$refs.music.musicPath = data.star_info.star_audio_mini // require('../assets/a.mp3') // data.star_info.star_audio
        /* eslint-disable */
        get.boolean = true
      })
    }
  },
  mounted () {
    QRCode.toDataURL((window.location.href.split('#')[0] + '&sharepic').replace(/&code.*?(?=\&)/, '').replace(/&state.*?(?=\&)/, ''))
      .then(url => {
        this.qrUrl = url
        console.log(url, 'asdasd')
        setTimeout(() => {
        }, 200)
      })
      .catch(err => {
        console.error(err, 'asderr')
      })
    this.getData()
  }
}
</script>

